<template>
	<div class="saleedi-address-list">
		<van-radio-group v-model="addressSelect" class="radio-group-warp" @change="addressChange">
			<div class="address-block fx-edi-block" v-for="(item, index) in addressListData" :key="index">
				<van-radio :name="item.id" class="radio-box" v-show="$route.query.select"></van-radio>
				<div class="address-content">
					<div class="address-row">
						<span class="name-value">{{item.consignee}}</span>
						<span class="tel-value">{{item.telePhone}}</span>
						<van-tag round type="primary" class="default-icon" v-show="item.defaultFlag==='1'">默认</van-tag>
					</div>
					<div class="address-row fx-ellipsis">{{item.region}}   {{item.address}}</div>
				</div>
				<div class="edit-box" @click="editClick(item)">
					<i class="edit-icon"></i>
				</div>
			</div>
		</van-radio-group>
		<div class="address-bottom">
			<van-button class="add-btn" type="primary" round @click="addAddressClick">+ 新增收货地址</van-button>
		</div>
	</div>
</template>

<script>
export default {
	name: 'addressList',
	data () {
		return {
			addressListData: [],
			addressSelect: ''
		}
	},
	created () {
		if (this.$route.query.select && this.$route.query.defaultId) {
			this.addressSelect = this.$route.query.defaultId
		}
		this.getAllReceipt()
	},
	methods: {
		getAllReceipt () {
			this.$fxHttp.requestPost({
				url: '/getAllReceipt'
			}).then(res => {
				this.addressListData = res
			})
		},
		addAddressClick () {
			this.$router.push('/addAddress')
		},
		editClick (item) {
			this.$router.push({
				path: '/addAddress',
				query: {
					edit: true,
					id: item.id,
					consignee: item.consignee,
					telePhone: item.telePhone,
					defaultFlag: item.defaultFlag,
					address: item.address,
					region: item.region
				}
			})
		},
		addressChange (id) {
			let selectItem = this.addressListData.find(item => {
				return item.id === id
			})
			this.$fxEventBus.$emit('changeSelectAddress', selectItem)
		}
	}
}
</script>
